import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { ButtonsContainer} from "./styled";
import { Button } from "./../styled";
import {
    selectTasks,
    selectIsHidingEnabled,
    toggleIsHidingEnabled,
    doAllTasks,
    removeAllTasks,
} from "./../../tasksSlice";

const Buttons = () => {
    const tasks = useSelector(selectTasks);
    const isHidingEnabled = useSelector(selectIsHidingEnabled);
    const dispatch = useDispatch();

    return (
        <ButtonsContainer>
            {tasks.length > 0 && (
                <>
                    <Button
                        disabled={tasks.every(({ done }) => !done)}
                        onClick={() => dispatch(toggleIsHidingEnabled())}
                    >
                        {isHidingEnabled ? "Poka≈º" : "Ukryj"} uko≈Ñczone
                    </Button>
                    <Button
                        disabled={tasks.every(({ done }) => done)}
                        onClick={() => dispatch(doAllTasks())}
                    >
                        Uko≈Ñcz wszystkie
                    </Button>
                    <Button
                        onClick={() => dispatch(removeAllTasks())}
                    >
                        Usu≈Ñ wszystkie
                    </Button>
                </>
            )}
        </ButtonsContainer>
    );
};
export default Buttons;